.checkbox,
.radio {
    margin: 10px;
}

.checkbox,
.radio {
    position: relative;
    label {
        &:hover {
            cursor: pointer;
        }
    }
}

.label-check label {
    padding-left: 20px;
}

.label-check input[type="radio"],
.label-check input[type="checkbox"] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    opacity: 0;
    position: absolute;
    margin: 0;
    z-index: -1;
    width: 0;
    height: 0;
    overflow: hidden;
    left: 0;
    pointer-events: none;
}

.label-check input[type="radio"]:focus {
    outline: none;
}

.label-check input[type="radio"]+label:before,
.label-check input[type="radio"]+label:after {
    content: "";
    display: block;
    position: absolute;
    left: 0px;
    top: 1px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    -webkit-transition: 240ms;
    -o-transition: 240ms;
    transition: 240ms;
}

.label-check input[type="radio"]+label:before {
    left: 0px;
    top: 2px;
}

.label-check input[type="radio"]+label:before {
    background-color: #fc4d18;
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    -o-transform: scale(0);
    transform: scale(0);
}

.label-check input[type="radio"]+label:after {
    top: 2px;
    border: 2px solid #fc4d18;
    z-index: 1;
}

.label-check input[type="radio"]:checked+label:before {
    -webkit-transform: scale(0.6);
    -ms-transform: scale(0.6);
    -o-transform: scale(0.6);
    transform: scale(0.6);
}

.label-check input[type="radio"]:disabled:checked+label:before {
    background-color: #bbbbbb;
}

.label-check input[type="radio"]:checked+label:after,
.label-check input[type="radio"]:hover+label:after {
    border-color: #fc4d18;
}

.label-check input[type="radio"]:disabled+label:after,
.label-check input[type="radio"]:disabled:checked+label:after {
    border-color: #bbbbbb;
}

.label-check input[type="checkbox"]:focus {
    outline: none;
}

.label-check input[type="checkbox"]:focus+label:after,
.label-check input[type="checkbox"]:hover+label:after {
    border-color: $red;
}

.label-check input[type="checkbox"]+label:after {
    content: "";
    position: absolute;
    top: 2px;
    left: -10px;
    display: block;
    width: 18px;
    height: 18px;
    margin-top: 4px;
    margin-right: 5px;
    border: 2px solid $brown-light;
    border-radius: 2px;
    -webkit-transition: 240ms;
    -o-transition: 240ms;
    transition: 240ms;
}

.label-check input[type="checkbox"]:checked+label:before {
    content: "";
    position: absolute;
    top: 8px;
    left: -4px;
    display: table;
    width: 6px;
    height: 12px;
    border: 2px solid $brown-light;
    border-top-width: 0;
    border-left-width: 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
    z-index: 1;
}

.label-check input[type="checkbox"]:checked+label:after {
    background-color: $red;
    border-color: $red;
}

.label-check input[type="checkbox"]:disabled+label:after {
    border-color: #bbbbbb;
}

.label-check input[type="checkbox"]:disabled:checked+label:after {
    background-color: #bbbbbb;
    border-color: transparent;
}