//Blog listing

#blog-listing, .similar-posts  {
	@extend #blogs-section;
	padding-top: 70px;
	padding-bottom: 105px;
	@include bp(desktop) {
		padding-bottom: 90px;
    }
    @include bp(lg) {
        padding-bottom: 80px;
    }
    @include bp(md) {
        padding-bottom: 70px;
    }
    @include bp(sm-down) {
        padding-bottom: 60px;
    }
	.blog-box {
		margin-bottom: 52px;
		@include bp(sm-down) {
	        margin-bottom: 30px;
	    }
		.blog-content {
			h2 {
				text-align: left;
				letter-spacing: 0;
			}
			a {
				color:$yellow;
			}
		}
	}
	.pagination {
		margin-top: 60px;
		@include bp(sm-down) {
	        margin-top: 30px;
	    }
	}
}

.similar-posts {
	&.first-child-box {
		padding-bottom: 0;
	}
	.blog-photo {
		height: 330px;
	}
	.blog-content {
		min-height: 287px;
		@include bp(sm-down) {
	        min-height: 0;
	    }
	}
}

//Blog single

#blog-detailed {
	@extend #regular-page;
	padding-top: 120px;
	padding-bottom: 30px;
	@include bp(desktop) {
		padding-top: 100px;
    }
    @include bp(lg) {
        padding-top: 90px;
    }
    @include bp(md) {
        padding-top: 70px;
        padding-bottom: 60px;
    }
    @include bp(sm-down) {
    	padding-top: 50px;
        padding-bottom: 50px;
    }
	h2 {
		margin-bottom: 65px;
		text-align: left;
		@include bp(md) {
	        margin-bottom: 50px;;
	    }
	    @include bp(sm-down) {
	    	margin-bottom: 40px;
    	}
		&:first-of-type {
			margin-top: 0;
		}
	}
	h3 {
		margin-top: 65px;
		text-align: left;
		@include bp(md) {
	        margin-top: 50px;
	    }
	    @include bp(sm-down) {
	    	margin-top: 40px;
    	}
	}
    .blog-photo {
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        margin-top: 65px;
        margin-bottom: 60px;
        @include bp(md) {
	        margin-top: 50px;
	        margin-bottom: 45px;
	    }
	    @include bp(sm-down) {
	    	margin-top: 40px;
	    	margin-bottom: 35px;
		}
		img {
			display:block;
			margin:0px auto;
			border-radius:12px;
			width: 80%;
		}
        span {
        	@include regular-text;
        	font-family: $fil-light;
        	display: block;
        	margin-top: 12px;
        	margin-bottom: 0;
		}
		.image__caption {
			text-align: center;
		}
	}
	// Video
	.video__holder {
		margin-bottom: 30px;
		iframe {
			max-width: 100%;
		}
	}
	.main__content {
		margin:0px auto;
		iframe {
			max-width: 100%;
			margin:0px auto;
			display:block;
		}	
		p {
			max-width: 100%;
		}
	}
	// accordion
	.accordion-section {
		.panel > div {
			display: none;
			padding: 15px;
		}
		h4 {
			text-align: left;
			border-radius:12px;
			padding:15px;
			font-size: 1.2em;
			border:none;
			padding-left: 30px;
			margin-top: 0px;
			margin-bottom: 15px;
			background:#3c2929;
			color:#dac4ae;
			@include animate-fade;
			@include bp(sm-down) {
				padding-left: 20px;
			}
			&:hover {
				cursor: pointer;
			}
		}
	}
	.single-table {
        margin-bottom: 30px;
		margin-left: auto;
		margin-right: auto;
	
        @include bp(sm-down) {
            display: block;
            overflow-x: auto;
            white-space: nowrap;
        }
        th {
            border: 1px solid #E0E0E0;
            background: rgba(212, 22, 21, 0.3);
            color:#E0E0E0;
            padding: 3px 10px;
        }
        td {
            border: 1px solid #E0E0E0;
            padding: 3px 10px;
			color:#E0E0E0;
        }
    }
}

// CTA
#blog-cta {
	margin-bottom: 60px;
	.cta-wrapper {
		background: #3c2929;
		border-radius: 20px;
		padding:40px;
		ul.nav-tabs {
			text-align: center;
			display: flex;
			justify-content: center;
			border:none;
			margin-bottom: 25px;
			li {
				margin-left: 15px;
				margin-right: 15px;
				border:none;
				font-family: $fil-bold;
				@include bp(md) {
					margin-left: 5px;
					margin-right: 5px;
				}
				a {
					border:none;
					color:$white;
					font-size: 18px;
					display: block;
    				padding: 0.5rem 1rem;
					@include bp(md) {
						font-size: 16px;
					}
					&.active {
						background:#4e3a3a;
						border-radius: 30px;
						color:#d4a629!important;
					}
				}
			}
		}
		.tab-pane {
			display: none;
			&.active {
				display: block;
			}
			button[type="submit"] {
				@include form-btn;
				@include bp(md-down) {
					width: 100%;
					margin-bottom: 30px;
				}
			}
			.control--checkbox {
				color:$white;
				a {
					color:$yellow;
				}
			}	
			
			.control--checkbox {
				font-size: 14px;
				color: $red;
				display: block;
				margin-bottom: 5px;
				padding-left: 0px;
				margin-left: 0px;		
				font-size: 15px!important;
			}
		
			.zebra_tooltips_below {
				color: #dd3333;
				text-decoration: underline;
				font-size: 16px;
			}
		
			.control__indicator {
				height: 16px;
				width: 16px;
				top: 1px!important;
				&:after {
					left: 5px;
					top: 1px;
				}
			}
		
			.control-group {
				display: inline-block;
				vertical-align: top;
				background: #fff;
				text-align: left;
				box-shadow: 0 1px 2px rgba(0,0,0,0.1);
				padding: 30px;
				width: 200px;
				height: 210px;
				margin: 10px;
			  }
			  .control {
				display: block;
				position: relative;
				padding-left: 40px;
				cursor: pointer;
				font-size: 18px;
				margin-right: 20px;
			  }
			  .control input {
				position: absolute;
				z-index: -1;
				opacity: 0;
			  }
			  .control__indicator {
				position: absolute;
				top: -5px;
				left: 0;
				height: 24px;
				width: 24px;
				background: #fff;
				border:1px solid #afafb0;
			  }
			  .control--radio .control__indicator {
				border-radius: 50%;
			  }
			  .control:hover input ~ .control__indicator,
			  .control input:focus ~ .control__indicator {
				background: #ccc;
			  }
			  .control input:checked ~ .control__indicator {
				background: $red; /*$hover*/
			  }
			  .control:hover input:not([disabled]):checked ~ .control__indicator,
			  .control input:checked:focus ~ .control__indicator {
				background:#000; /*$hover*/
				border:1px solid #000; /*$hover*/
			  }
			  .control input:disabled ~ .control__indicator {
				background: #e6e6e6;
				opacity: 0.6;
				pointer-events: none;
			  }
			  .control__indicator:after {
				content: '';
				position: absolute;
				display: none;
				-webkit-box-sizing: content-box;
				-moz-box-sizing: content-box;
				 box-sizing: content-box; 
			  }
			  .control input:checked ~ .control__indicator:after {
				display: block;
			  }
			  .control--checkbox .control__indicator:after {
				left: 9px;
				top: 5px;
				width: 3px;
				height: 8px;
				border: solid #fff;
				border-width: 0 2px 2px 0;
				transform: rotate(45deg);
			  }
			  .control--checkbox input:disabled ~ .control__indicator:after {
				border-color: #7b7b7b;
			  }
			  
			  .control--checkbox {
				.control__indicator {
				  top: -3px;
				}
			  }
			  
			  .control--radio .control__indicator {
				top: -2px;
			  }
			  .control--radio .control__indicator:after {
			   left: 7px;
				top: 7px;
				height: 8px;
				width: 8px;
				border-radius: 50%;
				background: #fff;
			  }
			  .control--radio input:disabled ~ .control__indicator:after {
				background: #7b7b7b;
			  }	

			  span.label {
				  display: block;
				  color:$white;
				  margin-bottom: 5px;
			  }
		

			.form-group {
				margin-bottom: 30px;
				@include bp(sm-down) {
					margin-bottom: 10px;
				}
				input[type="text"], input[type="email"], input[type="tel"], input[type="number"] {
					width: 100%;
					height: 61px;
					border-radius: 12px;
					background: #dac4ae;
					border: 1px solid #ddd;
					box-shadow: 5px 5px 10px rgba(0, 0, 0, .3);
					font-family: "Filson Soft Regular";
					font-size: 1em;
					color: #724e4e;
					padding: 0 80px 0 40px;
				}

				// select
				.bootstrap-select {
					width: 100%;
				}
				button.btn {
					width: 100%;
					border-radius: 12px;
					background: #dac4ae;
					border: 1px solid #ddd;
					box-shadow: 5px 5px 10px rgba(0, 0, 0, .3);
					font-family: "Filson Soft Regular";
					font-size: 1.375;
					color: #724e4e;
					height: 61px;
					line-height: 61px;
					padding: 0 80px 0 40px;
					&:focus {
						outline: 0!important;
					}
					.filter-option-inner {
						height: 61px;
						line-height: 61px;
					}
				}
				.dropdown-menu {
					border-radius:12px;
				}

				.dropdown-item.active, .dropdown-item:active {
					background: transparent!important;
					color:$red;
				}

				.dropup .dropdown-toggle:after, .dropdown-toggle:after {
					@include centre(y);
					right:15px;
				}

			}
		}
	}
}

// Cta
.quote-cta--single {
	border-radius: 20px;
	padding:30px;
	background:$brown-light;
	display: flex;
	justify-content: center;
	align-items: center;
	padding-top: 50px;
	padding-bottom: 50px;
	margin-bottom: 30px;
	@include bp(lg-down) {
		display: block;
		text-align: center;
	}
	span.title {
		display: block;
		color:$red;
		font-size: 42px;
		margin-right: 30px;
		display: block;
		line-height: 1.3;
		@include bp(lg-down) {
			margin-right: 0px;
			margin-bottom: 20px;
			font-size: 36px;
		}
	}
	.btn-cta {
		@include red-button;
	}
}
.blog-filters {
	margin-top: 50px;
	@include bp(lg-down) {
		margin-top: 40px;
	}
	ul {
		li {
			display: inline-block;
			vertical-align: middle;
			margin-right: 25px;
			margin-bottom: 15px;
			@include bp(sm-down) {
				margin-top: 5px;
				margin-bottom: 5px;
				margin-right: 10px;
			}
			&:last-of-type {
				margin-right: 0;
			}
			a {
				display: block;
				color: $brown-dark;
				background-color: $brown-light;
				border-radius: 8px;
				padding: 5px 32px;
				text-transform: uppercase;
				text-align: center;
				&.active, &:hover {
					background-color: $red;
					color: $brown-light;
				}
				@include bp(sm-down) {
					font-size: 0.7em;
				}
			}	
		}
	}
}
.author-desc {
	display: flex;
	margin-bottom: 40px;
	.author-content {
		align-self: center;
		a {
			color: $red;
			&:hover {
				color: $yellow;
				text-decoration: none;
			}
		}
		p {
			margin-bottom: 0;
			font-size: 12px;
			font-style: italic;
			line-height: 1.3;
			color: $brown-light;
		}
	}
	img {
		width: 60px;
		height: 60px;
		object-fit: cover;
		border-radius: 50%;
		border: 2px solid;
		margin-right: 20px;
	}
} 
.featured-article-box {
    .blog-box {
        background-color: $brown-light;
        padding: 35px;
        border-radius: 17px;
        margin-bottom: 30px;
        .blog-photo {
            display: inline-block;
            width: 30% !important;
            vertical-align: middle;
            margin: 0 !important;
            margin-right: 30px;
            height: 150px;
            @include bp(sm-down) {
                width: 100% !important;
                margin-bottom: 20px !important;
            }
            img {
                max-width: 100% !important; 
                height: 100%;
                object-fit: cover;
            }
        }
        .blog-content {
            display: inline-block;
            width: 60%;
            vertical-align: middle;
            margin-left: 30px;
            @include bp(sm-down) {
                margin-left: 0;
                width: 100%;
                text-align: center;
            }
			h3 {
				margin-top: 0 !important;
				margin-bottom: 20px !important;
				font-size: 1.8em !important;
				@include bp(sm-down) {
					text-align: center;
					font-size: 1.2em !important;
				}
				a {
					color: $red;
				}
            } 
			.btn-cta {
				display: inline-block;
				@include red-button;
				height: 50px;
				font-size: 1em;
				@include bp(sm-down) {
					text-align: center;
				}
			}
        }
    }
}

#nav-slider {
	.item {
		padding-left: 10px;
		padding-right: 10px;
		.nav-col {
			position: relative;
			border-radius: 20px;
			background: rgba(62,54,65,0.52);
			margin-bottom: 32px;
			text-align: center;
			padding: 30px;		
			&:hover {
				span {
					color:#dac4ae;
				}
			}
			.icon {
				height: 120px;
				img {
					@include centre(both);
					width: 80px;
					filter: brightness(0) invert(1);

				}
			}
			span {
				display: block;
				font-family: $fil-med;
				font-size: 1.625em;
				line-height: 1.385;
				text-align: center;
				color: $yellow;
				margin-bottom: 35px;
				@include animate-fade;
				@include animate-fade;
				@include bp(desktop) {
					font-size: 1.5em;
				}
				@include bp(lg) {
					font-size: 1.4em;
				}
				@include bp(md) {
					font-size: 1.4em;
				}
				@include bp(sm-down) {
					font-size: 1.4em;
				}				
			}	
		}
	}
}